.company {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background: white; }

.el-company {
  font-family: "Circe";
  flex: 0 0 250px;
  font-size: 16px;
  line-height: 160%;
  color: black; }
  @media (max-width: 1100px) {
    .el-company {
      flex: 0 0 50%;
      padding-right: 15px; } }
  @media (max-width: 600px) {
    .el-company {
      flex: 0 0 100%; } }
  .el-company:nth-child(1), .el-company:nth-child(2) {
    margin-bottom: 70px; }
    @media (max-height: 820px) {
      .el-company:nth-child(1), .el-company:nth-child(2) {
        margin-bottom: 25px; } }
  .el-company__img {
    margin-bottom: 28px; }
    @media (max-height: 820px) {
      .el-company__img {
        margin-bottom: 15px; } }
    .el-company__img img {
      max-width: 90px;
      max-height: 72px; }
      @media (max-height: 820px) {
        .el-company__img img {
          max-height: 50px; } }
  .el-company__title {
    font-weight: 700;
    line-height: 1em;
    margin-bottom: 25px; }
    @media (max-height: 820px) {
      .el-company__title {
        margin-bottom: 15px; } }
  .el-company__desc {
    max-width: 240px; }
    @media (max-height: 820px) {
      .el-company__desc {
        line-height: 1.1em; } }
    @media (max-width: 600px) {
      .el-company__desc {
        max-width: 100%; } }
